<template>
  <div class="personal-content-body">
    <div class="content">
      <div class="content-title clear">
        <div class="left">{{name}}（{{phone}}）登录记录</div>
        <!-- <div class="search-box right">
        <input class="search-box-input" type="text" placeholder="搜索" @change="keyWordChange" v-model="keyWord">
        <span class="search-box-icon icon-search"></span>
      </div>
      <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :clearable='false'  value-format="yyyy-MM-dd" @change="toselectTime" class="header-right-time right">
      </el-date-picker> -->
        <el-input placeholder="搜索" v-model="keyWord" class="header-right-time right"  :clearable='true' @change="getListData(1)">
          <i slot="prefix" class="el-input__icon el-icon-search "></i>
        </el-input>
        <el-date-picker v-model="selectTime" type="date" placeholder="选择日期" :editable='false' :clearable='true' value-format="yyyy-MM-dd" @change="getListData(1)" class="header-right-time right">
        </el-date-picker>
      </div>
      <div class="historyapply-group">
        <div class="historyapply-group-total">共{{total}}条登录记录</div>
        <div class="historyapply-group-tablebox">
          <div class="table-normal-box">
            <table class="table-normal">
              <tr>
                <th>序号</th>
                <th>登录IP</th>
                <th>登录地点</th>
                <th>登录时间</th>
              </tr>
              <tr v-for="item in list" :key="item.seq">
                <td>{{item.seq}}</td>
                <td>{{item.ip}}</td>
                <td>{{item.site}}</td>
                <td>{{item.login_time}}</td>
              </tr>
            </table>
            <div class="table-nodata" v-if="total==0">
              <img src="../../images/table_nodata.png"> 没有对应数据
            </div>
          </div>
          <div class="pagination-normal" v-if="total!=0">
            <el-pagination background layout="prev, pager, next" :total="total" :page-size="pageSize" prev-text="上一页" next-text="下一页" @current-change="currentChange" :current-page="currentPage">
            </el-pagination>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  userSafeInfo,
} from '../../config/api'
export default {
  data() {
    return {
      list: [],
      pageSize: 10,
      selectTime: null,
      searchType: null,
      keyWord: null,
      total: null,
      currentPage: 1,
      name: null,
      phone: null,
    }
  },
  mounted() {
    this.getListData(1);
  },
  methods: {
    /*keyWordChange() {
      this.list = [];
      // this.currentPage = 1;
      this.searchType = 'keyWord'
      this.getListData();
    },
    //选择日期
    toselectTime() {
      console.log(this.selectTime);
      this.list = [];
      // this.currentPage = 1;
      // this.busy = false;
      this.searchType = 'time';
      this.getListData();
    },*/
    currentChange(val) {
      if (this.total > this.pageSize) {
        this.getListData(val)
      }
    },
    //获取数据
    getListData(val) {
      this.list = [];
      let params = {
        page: val,
        number: this.pageSize,
      }
      if (this.selectTime != null && this.selectTime != '') {
        params.login_time = this.selectTime
      }
      if (this.keyWord != null && this.keyWord != '') {
        params.search = this.keyWord;
      }
      userSafeInfo(params).then(res => {
        let data = res.data.payload;
        this.total = data.total;
        this.name = data.name;
        this.phone = data.phone;
        if (data.data.length != 0) {
          this.list = data.data;
        }
      })
    },
  }
}

</script>
<style lang="scss" scoped>
.content {
  width: 100%;
  min-height: 100%;
  background-color: white;
  padding: 40px 30px;
}
.content-title {
  border-bottom: 1px solid #E4E4E4;
}
// .content-title>div.left {
//   font-size: 18px;
//   line-height: 30px;
//   color: #35383A;
//   margin-bottom: 23px;
//   font-weight: bold;
// }
.histroyapply-icon {
  width: 15px;
  height: 15px;
  background: url('../../images/histroyapply_icon.png') no-repeat;
  display: inline-block;
  float: left;
  margin: 2px 6px 0px 0px;
}

.historyapply-group {
  width: 100%;
  // border: 1px solid #E4E4E4;
  padding: 18px 0px 18px 0px;
}

.historyapply-group-total {
  font-size: 14px;
  color: #3C98E8;
  font-weight: bold;
  line-height: 1em;
  margin-bottom: 20px;
}

.historyapply-group-tablebox {
  // padding: 0px 18px 0px 0px;
  min-height: 440px;
}

.table-normal-box {
  min-height: 440px;
}

.pagination-normal {
  text-align: center;
  margin-top: 20px;
}

.header-right-time {
  margin-left: 10px;
  position: relative;
}

</style>



